@media (max-width: 767px) {
    .controls {
        padding-right: 45px;
    }
    .panel {
        border-radius: 0;
        .panel-heading {
            border-radius: 0;
            border: none;
        }
    }
}

@media (min-width: 768px) {
    .controls {
        float: right;
        padding-right: 45px;
        width: 245px;
    }
}

.controls-text {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    padding-top: 7px;
}

body {
    overflow: hidden; 
    .controls-xs {
        width: 50px;
    }

    .controls-sm {
        width: 150px;
    }

    .controls-md {
        width: 300px;
    }

    .controls-lg {
        width: 550px !important;
    }

    .control-xs {
        width: 40px;
    }

    .control-sm {
        width: 80px;
    }

    .control-md {
        width: 150px;
    }

    .control-fit {
        width: 130px;
    }

    .control-lg {
        width: 300px;
    }
}
.dl-horizontal dt {
    width: 140px !important;
}

.dl-horizontal dd {
    margin-right: 15px;
}

.dl-inline dd {
    float: left;
    padding-left: 0;
    margin-left: 0;
}

.field-label {
    font-weight: normal;
    padding-top: 7px;
    color: #d82228;
}

table thead {
    background: #f3f3f3;
}

select.form-control {
    padding-top: 4px;
}

.form-control, .input-append input{
    @media (max-width: 480px) {
        max-width: 100%;
        width: 150px;
    }
    @media (min-width: 480px) {
        width: 220px;
    }
    @media (min-width: 768px) {
        width: 240px;
    }
}
.modal .form-control, .modal .input-append input {
    max-width: 100%;
}

.th-checkbox {
    width: 30px;
}

.th-index {
    width: 60px;
}

.controls-inline {
    width: 100% !important;
    &>input {
        display: inline-block;
    }
    &>span {
        display: inline;
    }
}

.help-inline {
    display: inline;
}

table td .operate {
    cursor: pointer;
    span {
        background: url(/w20e/common/img/operate.png) no-repeat;
        padding: 10px 12px;
        &.enable {
            background-position: 0 12px;
        }
        &.disable {
            background-position: 0 -37px;
        }
        &.edit {
            background-position: 0 -83px;
        }
        &.delete {
            background-position: 0 -134px;
        }
    }
}

button.btn-del {
    background-image: url(/w20e/common/img/operate.png);
    padding-left: 31px;
    background-repeat: no-repeat;
    background-position: 6px -142px;
    &:focus {
        background-image: url(/w20e/common/img/operate.png);
    }
}

.btn {
    &.pageNum, &.prev, &.next {
        display: inline-block;
        line-height: 25px;
        width: 32px;
        margin-left: 3px;
        padding: 1px 2px;
        color: #555;
        cursor: pointer;
        -webkit-border-radius: 6px;
        border-radius: 6px;
        text-align: center;
    }
}

.pageGo {
    display: inline-block;
    margin-bottom: -12px;
}

.dl-horizontal {
    +.dl-horizontal {
        border-top: 1px solid #F2F2F2;
        padding-top: 18px;
    }
}

label {
    &.file {
        position: relative;
        input[type='file'] {
            opacity: 0;
            background: transparent;
            height: 100%;
            width: 100%;
            filter: alpha(opacity=0);
            height: 0\0;
            width: 0\0;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
}

.btn-static-sm {
    min-width: 80px;
}

.modal-backdrop {
    background: #000;
}
.modal-backdrop.in {
    background: #000;
    opacity: 0.3;
    filter: alpha(opacity=30);
}

@media (min-width: 768px) {
    .checkbox-list[id="appFilterList"] {
        label {
            width: 121px;
            float: left;
        }
    }
    .checkbox-list[id="urlFilterGroupList"] {
        label {
            width: 116px;
            float: left;
        }
    }
}

@media (max-width: 767px) {
    .checkbox-list[id="appFilterList"] {
        label {
            width: 95px;
            float: left;
        }
    }
    .checkbox-list[id="urlFilterGroupList"] {
        label {
            width: 90px;
            float: left;
        }
    }
}

.modal-backdrop.fade {
    -webkit-transition: opacity 0.1s linear;
            transition: opacity 0.1s linear;
}

.modal-dialog {
    &.modal-md {
        width: 455px;
    }
    &.modal-lg {
        width: 600px;
    }
}

.modal-body {
    /* max-height: 400px; */
    /* overflow-y: auto; */
    /*
    .control-label {
        width: 135px;
    }
    */
    @media (max-width: 600px) {
        overflow-x: auto; 
    }
    .controls {
        padding-left: 150px;
        width: 350px;
    }
}

.modal-footer {
    border-top: none;
}

a {
    cursor: pointer;
}

.msgbox {
    position: fixed;
    top: 140px;
    left: 50%;
    margin-left: -119px;
    width: 222px;
    padding: 28px 8px;
    color: #fff;
    font-size: 14px;
    z-index: 9999;
    text-align: center;
    -webkit-border-radius: 5px;
    border-radius: 5px;
    background-color: #666;
    opacity: .9;
    filter: alpha(opacity=90);
}

.text-static {
    text-overflow: ellipsis;
    overflow: hidden;
    word-wrap: keep-all;
    white-space: nowrap;    
}

td span.text-static {
    max-width: 400px;
    display: inline-block;
}

.table-page-navbtn {
    text-align: right;
}

.table-page-navbtn label {
    display: none;
}

.table-page-navbtn .pageGo {
    display: none;
}